<template>
  <div id="app">
  	<top-bar></top-bar>
  	<left-slider></left-slider>
  	<router-view></router-view>
  </div>
</template>

<script>
import leftSlider from 'base/left-sliderBar/left-slider';
import topBar from 'base/top-bar/top-bar';

export default {
	components:{leftSlider,topBar},
	mounted(){
		if(window.route !== '{{ route }}'){
			var arr_url = window.route.split('/');
			var index = arr_url.length -1;
			this.$router.push({name:arr_url[index]});
		}else{
			this.$router.push({name:'home'});
		}
	}
}
</script>

<style>
/*	#app{
		height: 100%;
	}*/
</style>



