<template>
	<div class="center-cont" :class="{'center-pull-left':isPull,'center-extend-left':!isPull}">
		<router-view></router-view>
	</div>
</template>

<script>
	export default {
		computed:{
			isPull(){
				return this.$store.state.isPull;
			}
		}
	}
</script>

<style scoped lang='less'>
	@import "~common/less/variable";
	.center-cont{
		padding-top: 60px;
	}
	.center-pull-left{
		padding-left: 60px;
	}
	.center-extend-left{
		padding-left: 220px;
	}
</style>


