<template>
	<div class="top-bar p-f">
		<div class="nav-bar f-r">
			<Menu mode="horizontal" class='top-bar-menu' theme="dark">
		        <MenuItem name="1" class='top-bar-item'>
		            <Icon type="ios-folder-outline"></Icon>
		            备案
		        </MenuItem>
		        <MenuItem name="2" class='top-bar-item'>
		            <Icon type="clipboard"></Icon>
		            工单
		        </MenuItem>
		        <Submenu name="3" class='top-bar-item'>
		            <template slot="title">
		                <Icon type="ios-person"></Icon>
		                bangzhongcao
		            </template>
	                <MenuItem name="3-1">退出</MenuItem>
		                
		           <!--  <MenuGroup title="留存">
		                <MenuItem name="3-4">用户留存</MenuItem>
		                <MenuItem name="3-5">流失用户</MenuItem>
		            </MenuGroup> -->
		        </Submenu>
		    </Menu>
		</div>
	</div>
</template>

<script>
	export default {};
</script>

<style scoped lang='less'>
	@import "~common/less/variable";
	.top-bar{
		top:0;
		width: 100%;
		height: 61px;
		border-bottom: 1px solid @borderColor;
		// background-color: @darkBackground;
		background-color: @blackBackground;
		z-index: 100;
		.nav-bar{
			margin-right: 20px;
			.top-bar-menu{
				background-color: @blackBackground;
				.top-bar-item{
					font-size: 16px;
				}
			}
		}
	}
</style>


