<template>
    <div class='detail-menu p-f' :class="{'shrink_menu':!showMenu,'detail-menu-pull':isPull,'detail-menu-extend':!isPull}">
        <ul v-show='showMenu'>
        	<li>
        		<div class="menu-title">{{sliderItem.title}}</div>
        		<ul>
        			<li class='li-link c-p' v-for='(key,value) in sliderItem.data'>
		            	<router-link tag='div' :to='key'>
			            	<Icon type="android-radio-button-on"></Icon>
			            	<span>{{value}}</span>
		            	</router-link>
		            </li>
        		</ul>
        	</li>
        </ul>
        <div class="shrink-btn p-a ta-c c-p" :class="{rotate_shrink_btn:!showMenu}"
        	@click='showDetailMenu(showMenu)'><Icon type="navicon"></Icon></div>
    </div>
</template>

<script>
    export default {
    	props:['isShow','sliderItem'],
    	data(){
    		return{
    			showMenu:this.isShow
    		}
    	},
    	computed:{
			isPull(){
				return this.$store.state.isPull;
			}
		},
    	methods:{
    		showDetailMenu(){
    			this.showMenu = !this.showMenu;
    			this.$emit('hidden',this.showMenu);
    		}
    	}
    };
</script>

<style scoped lang='less'>
	@import '~common/less/variable';
	@import '~common/less/mixin';

	#app .shrink_menu{
		width: 1px !important;
		.rotate_shrink_btn{
			transform:rotate(180deg);
			right: -19px;
		}
	}
	.detail-menu{
		height: 100%;
		width: 160px !important;
		top:60px;
		background-color: @whiteBackground;
		.menu-title{
			padding-left: 10px;
			line-height: 47px;
			font-size: 16px;
			color:#909090;
		}
		.li-link{
			font-size: 14px;
			div{
				padding: 14px 24px;
				&.router-link-active{
					color: @logoColor;
	    			border-right: 2px solid @logoColor;
				}
				span{
					padding-left: 8px;
				}
			}
		}
		.shrink-btn{
			right: 0;
			top:50%;
			margin-top: -13px;
			width: 20px;
			height: 26px;
			line-height: 26px;
			font-size: 26px;
			font-weight: 700;
			background: @shrinkBtnColor;
			&:before{
				.shrink-menu-btn();
				border-bottom: 4px solid @shrinkBtnColor;
				border-top: 4px solid transparent;
				top:-8px;
				right: 0;
			}
			&:after{
				.shrink-menu-btn();
				border-top: 4px solid @shrinkBtnColor;
				border-bottom: 4px solid transparent;
				bottom:-8px;
				right: 0;
			}
		}
	}
	.detail-menu-pull{
		left: 60px;
	}

	.detail-menu-extend{
		left: 220px;
	}
</style>


