<template>
	<div class="content-panel">
        <div class="instance-list" v-show='!isCreate'>
            <div class="availzone_btn">
                <Button :type='bx_status' @click="changeAvaizone('北显')">北显</Button>
                <Button :type='yz_status' @click="changeAvaizone('亦庄')">亦庄</Button>
            </div>

            <!-- 创建实例，开机，关机，重启，删除 按钮 -->
            <div class='content-header'>
                <Button type="primary" size='large' @click='showCreatePanel'>
                    <Icon type="plus"></Icon>
                    创建实例
                </Button>
                <ButtonGroup size='large' class='operation-btns'>
                    <Button type="ghost" :disabled='!itemCount' icon='power'>开机</Button>
                    <Button type="ghost" :disabled='!itemCount' icon='record'>关机</Button>
                    <Button type="ghost" :disabled='!itemCount' icon='refresh'>重启</Button>
                    <Button type="ghost" :disabled='!itemCount' icon='trash-a'>删除</Button>
                </ButtonGroup>

                <!-- 更多操作 -->
                <Dropdown style="margin-left: 20px">
                    <Button type="ghost" size='large' :disabled='!itemCount'>
                        更多操作
                        <Icon type="ios-arrow-down"></Icon>
                    </Button>
                    <DropdownMenu slot="list">
                        <DropdownItem :disabled='!itemCount'>重装系统</DropdownItem>
                        <DropdownItem :disabled='!itemCount'>改名</DropdownItem>
                    </DropdownMenu>
                </Dropdown>

                <!-- 最右边的刷新，设置字表列段，导出按钮 -->
                <ButtonGroup shape='circle' size='large' class='f-r'>
                    <Button type="text" class='right-operate-btns' @click='refresh' title='刷新'>
                        <Icon type="loop" class='right-operate-icon'></Icon>
                    </Button>
                    <Button type="text" class='right-operate-btns' title='设置字表列段'>
                        <Icon type="gear-b" class='right-operate-icon'></Icon>
                    </Button>
                    <Button type="text" class='right-operate-btns' @click='exportData' title='导出全部'>
                        <Icon type="android-exit" class='right-operate-icon'></Icon>
                    </Button>
                </ButtonGroup>

                <!-- 搜索框 -->
                <!-- <Search class='f-r' v-model='searchValue' @search-item='Search'></Search> -->
                <div class="search f-r p-r ta-c">
                    <input type="text" v-model="searchValue" @keyup.enter='handleSearch(searchValue)' @input='emptySearch(searchValue)' placeholder='Search'>
                    <i class="search-btn p-a c-p ivu-icon ivu-icon-ios-search" @click='handleSearch(searchValue)'></i>
                </div>
            </div>
            <div class="content-table">
                <div class="top-pagination ta-r">
                    <Page :total="itemCount" :current='pageNum' :page-size="pageSize" :page-size-opts='opts' placement='top' show-total show-elevator show-sizer @on-change="changePage" @on-page-size-change='changePageSize'></Page>
                </div>
                <Table border ref='table' size='large' :loading="loading" :columns="columns" :data="CurrentData" @on-sort-change='sortChange'></Table>
                <div class="bottom-pagination ta-r">
                    <Page :total="itemCount" :current='pageNum' :page-size="pageSize" :page-size-opts='opts' placement='top' show-total show-elevator show-sizer @on-change="changePage" @on-page-size-change='changePageSize'></Page>
                </div>
            </div>
        </div>
        <div class="create-inst" v-show='isCreate'>
            <create-inst></create-inst>
        </div>
	</div>
	
</template>
<script>
	import Search from 'base/search-com/search-com';
    import createInst from './create-instance/create-instance';
    import {getInstanceList} from 'api/service';

	export default {
		components:{Search,createInst},
        data () {
            return {
            	bx_status:'primary',
            	yz_status:'ghost',
            	// value:'',
                isCreate:false,
                loading:true,//table处于等待状态
                searchValue:'',//搜索的值
                frontData:[],//可进行操作的数据
                pageSize:10,//当前每页展示的条数
                pageNum:1,//当前在第几页
                opts:[10,25,50],//每页条数切换的配置
                key:'',//排序的关键字
                order:'asc',//默认的排序的升降序
                columns: [
                    {
                        type: 'selection',
                        width: 60,
                        align: 'center'
                    },
                    {
                        title: '名称',
                        key: 'name',
                        sortable: true,
                        sortType: 'asc'
                    },
                    {
                        title: 'IP',
                        key: 'ip',
                        sortable: true
                    },
                    {
                        title: '项目',
                        key: 'project',
                        sortable: true
                    },
                    // {
                    //     title: '创建者',
                    //     key: 'create_by',
                    //     sortable: true
                    // },
                    {
                        title: '宿主机',
                        key: 'host',
                        sortable: true
                    },
                    {
                        title: '电源',
                        key: 'power_state',
                        sortable: true
                    },
                    // {
                    //     title: '存储卷',
                    //     key: 'volumes',
                    //     sortable: true
                    // },
                    {
                        title: '镜像',
                        key: 'os_type',
                        sortable: true
                    },
                    {
                        title: '状态',
                        key: 'status',
                        sortable: true
                    },
                    {
                        title: '类型(GB)',
                        key: 'flavor',
                        sortable: true
                    },
                    {
                        title: '机房',
                        key: 'availability_zone',
                        sortable: true
                    },
                    // {
                    //     title: '创建时间',
                    //     key: 'created',
                    //     sortable: true
                    // },
                    {
                        title: '操作',
                        key: 'action',
                        align: 'center',
                        width: 150,
                        render: (h, params) => {
                            return h('div', [
                                h('Button', {
                                    props: {
                                        type: 'primary'
                                    },
                                    style: {
                                        marginRight: '5px'
                                    },
                                    on: {
                                        click: () => {
                                            this.show(params.index)
                                        }
                                    }
                                }, '操作')
                            ]);
                        }
                    }
                ],
                listData: []
            }
        },
        computed:{
            CurrentData(){
                var _start = ( this.pageNum - 1 ) * this.pageSize;
                var _end = this.pageNum * this.pageSize;
                if(this.frontData.length<_end){
                    return this.frontData.slice(_start);
                }else{
                    return this.frontData.slice(_start,_end);
                }
                
            },
            itemCount(){
                return this.frontData.length;
            }
        },
        created(){
            // getInstanceList().then(res=>{
            //     console.log(res.body.data);
            //     this.data = res.body.data;
            // });
            this.$http.get('/api/instance-list').then(res=>{
                // this.listData = this.initData(res.body.data);
                this.listData = res.body.data;
                this.frontData = this.listData.slice(0);
                this.loading = false;
            })
        },
        methods:{
            // 整理初始化数据
            initData(inst_data){
                // 将flavor和volumes转成字符串
                for(let index in inst_data){
                    let volumes_list = inst_data[index].volumes;
                    let flavor_list = inst_data[index].flavor;
                    inst_data[index].volInfo = '';
                    inst_data[index].flavorInfo = 'Cpu:'+flavor_list.cpu+'内存：'+flavor_list.memory+'根磁盘：'+flavor_list.root_disk+'缓存：'+flavor_list.ephemeral_disk;
                    for(let i in volumes_list){
                        inst_data[index].volInfo += ('名称：'+volumes_list[i].name + '大小：'+ volumes_list[i].size);
                    }
                    delete inst_data[index].volumes;
                    delete inst_data[index].flavor;
                }
                return inst_data;
            },
            // 搜索功能
            handleSearch(value){
                var v = value.trim();
                // 将页码重设为1
                this.pageNum = 1;
                this.frontData = this.listData.filter(function(item){
                    // debugger
                    for(var key in item){
                        if(String(item[key])===v){
                            return item;
                        }
                    }
                });
                // 重新排序
                this.sortChange();
            },
            //当搜索框为空时自动返回所有数据
            emptySearch(value){
                if(!value){
                    this.frontData = this.listData.slice(0);
                    // 重新排序
                    this.sortChange();
                }
            },
            // 排序函数
            compare(prop,order) {
                return function (obj1, obj2) {
                    var val1 = obj1[prop];
                    var val2 = obj2[prop];
                    if (!isNaN(Number(val1)) && !isNaN(Number(val2))) {
                        val1 = Number(val1);
                        val2 = Number(val2);
                    }
                    if(order==='asc'){
                        if (val1 < val2) {
                            return -1;
                        } else if (val1 > val2) {
                            return 1;
                        } else {
                            return 0;
                        } 
                    }else if(order==='desc'){
                        if (val1 < val2) {
                            return 1;
                        } else if (val1 > val2) {
                            return -1;
                        } else {
                            return 0;
                        } 
                    }
                               
                } 
            },
            // 排序
            sortChange(obj){
                if(obj){
                    this.key = obj.key;
                    if(obj.order!=='normal'){
                        this.order = obj.order;
                    }
                }
                this.frontData.sort(this.compare(this.key,this.order));
            },
            // 翻页
            changePage(index){
                this.pageNum = index;
                window.scrollTo(0, 0);
            },
            // 修改每页显示条数
            changePageSize(size){
                this.pageSize = size;
                this.pageNum = 1;
                window.scrollTo(0, 0);
            },
            // 选择机房
        	changeAvaizone(str){
        		if(str==='北显'){
        			this.bx_status = 'primary';
        			this.yz_status = 'ghost';
        		}else if(str==='亦庄'){
    				this.bx_status = 'ghost';
        			this.yz_status = 'primary';
        		}
        	},
            // 刷新按钮
            refresh(){
                this.loading = true;
                this.$http.get('/api/instance-list').then(res=>{
                    // this.listData = this.initData(res.body.data);
                    this.searchValue = '';//清空搜索值
                    this.pageNum = 1;//当前页码为1
                    this.listData = res.body.data;
                    this.frontData = this.listData.slice(0);
                    this.sortChange();
                    this.loading = false;
                });
            },
            // 导出数据
            exportData(){
                this.$refs.table.exportCsv({
                    filename: '虚拟机列表数据',
                    columns: this.columns.filter((col, index) => index > 0),
                    data: this.frontData.filter((data, index) => index > 0)
                });
            },
            // 显示创建虚机面板
            showCreatePanel(){
                this.isCreate = true;
            }
        }
    }
</script>

<style scoped lang='less'>
	@import '~common/less/variable';

	.content-panel{
		.instance-list{
            .availzone_btn{
                padding: 8px 15px;
                background-color: @whiteBackground;
                border-bottom: 1px solid #efefef;
            }
            .content-header{
                // margin-top: 10px;
                padding: 12px 15px;
                background-color: @whiteBackground;
                .right-operate-btns{
                    padding: 5px 6px;
                    .right-operate-icon{
                        font-size: 22px;
                    }
                }
                .icon-size{
                    font-size: 16px;
                    margin-right: 5px;
                }
                .operation-btns{
                    margin-left: 15px;
                }
                .search{
                    width: 300px;
                    margin-right: 30px;
                    padding: 2px 35px 2px 8px;
                    border:1px solid #c0c0c0;
                    border-radius: 3px;
                    input{
                        width: 100%;
                        height: 30px;
                    }
                    .search-btn{
                        top: 0;
                        right: 0;
                        width: 34px;
                        line-height: 34px;
                        font-size: 22px;
                        background-color: #f0f0f0;
                        &:hover{
                            background-color: #ccc;
                        }
                    }
                }
            }
            .content-table{
                border-top: 10px solid @colorBackground;
                width: 100%;
                background-color: @whiteBackground;
                .change-page{
                    margin: 10px;
                    font-size: 14px;
                }
                .top-pagination{
                    padding: 10px;
                }
                .bottom-pagination{
                    padding:15px;
                }
            }
        }
        .create-inst{
            height: 100%;
        }
	}
</style>


