<template>
	<div class="create-instance">
		<Tabs>
	        <TabPane label="新建云服务器" icon="gear-a">
	        	<Steps :current="1" status="process" class='process'>
			        <Step title="选择配置"></Step>
			        <Step title="选择IP"></Step>
			        <Step title="设置VPC"></Step>
			        <Step title="设置基本信息"></Step>
			    </Steps>
	        </TabPane>
	        <TabPane label="新建专属云服务器" icon="ios-cog-outline">标签二的内容</TabPane>
	    </Tabs>
	    <div class="inst-panel p-r">
	    	<div class="choose-setting">
	    		<div class="create-step">
	    			<Form ref="formValidate" :model="formValidate" :rules="ruleValidate" :label-width="80">
				        <FormItem label="Name" prop="name">
				            <Input v-model="formValidate.name" placeholder="Enter your name" class='input-style'></Input>
				        </FormItem>
				        <FormItem label="E-mail" prop="mail">
				            <Input v-model="formValidate.mail" placeholder="Enter your e-mail" class='input-style'></Input>
				        </FormItem>
				        <FormItem label="City" prop="city">
				            <Select v-model="formValidate.city" placeholder="Select your city" class='input-style'>
				                <Option value="beijing">New York</Option>
				                <Option value="shanghai">London</Option>
				                <Option value="shenzhen">Sydney</Option>
				            </Select>
				        </FormItem>
				        <FormItem label="Radio">
				            <RadioGroup v-model="formItem.radio">
				                <Radio label="male">Male</Radio>
				                <Radio label="female">Female</Radio>
				            </RadioGroup>
				        </FormItem>
				        <FormItem label="Checkbox">
				            <CheckboxGroup v-model="formItem.checkbox">
				                <Checkbox label="Eat"></Checkbox>
				                <Checkbox label="Sleep"></Checkbox>
				                <Checkbox label="Run"></Checkbox>
				                <Checkbox label="Movie"></Checkbox>
				            </CheckboxGroup>
				        </FormItem>
				        <FormItem label="Switch">
				            <i-switch v-model="formItem.switch" size="large">
				                <span slot="open">On</span>
				                <span slot="close">Off</span>
				            </i-switch>
				        </FormItem>
				        <FormItem label="Slider">
				            <Slider v-model="formItem.slider" class='com-width' range></Slider>
				        </FormItem>
				        <FormItem label="Text">
				            <Input v-model="formItem.textarea" type="textarea" class='com-width' :autosize="{minRows: 2,maxRows: 5}" placeholder="Enter something..."></Input>
				        </FormItem>
				        
				        <div class="create-info p-a">
				        	<p class='info-title'>云服务器</p>
				        	<div class="info-panel">
				        		配置:	1核 2GB （IO优化型I1）
				        	</div>
				        	<div class="btn-group ta-l">
				        		<Button type="ghost">上一步</Button>
				        		<Button type="primary">下一步</Button>
				            	<Button type="ghost">取消</Button>
				        	</div>				        
				        </div>
			        </Form>
	    		</div>
	    	</div>
	    </div>
	</div>
</template>

<script>
	export default {
        data () {
            return {
            	formItem: {
                    input: '',
                    select: '',
                    radio: 'male',
                    checkbox: [],
                    switch: true,
                    date: '',
                    time: '',
                    slider: [0, 100],
                    textarea: ''
                },
                formValidate: {
                    name: '',
                    mail: '',
                    city: '',
                    gender: '',
                    interest: [],
                    date: '',
                    time: '',
                    desc: ''
                },
                ruleValidate: {
                    name: [
                        { required: true, message: 'The name cannot be empty', trigger: 'blur' }
                    ],
                    mail: [
                        { required: true, message: 'Mailbox cannot be empty', trigger: 'blur' },
                        { type: 'email', message: 'Incorrect email format', trigger: 'blur' }
                    ],
                    city: [
                        { required: true, message: 'Please select the city', trigger: 'change' }
                    ],
                    gender: [
                        { required: true, message: 'Please select gender', trigger: 'change' }
                    ],
                    interest: [
                        { required: true, type: 'array', min: 1, message: 'Choose at least one hobby', trigger: 'change' },
                        { type: 'array', max: 2, message: 'Choose two hobbies at best', trigger: 'change' }
                    ],
                    date: [
                        { required: true, type: 'date', message: 'Please select the date', trigger: 'change' }
                    ],
                    time: [
                        { required: true, type: 'date', message: 'Please select time', trigger: 'change' }
                    ],
                    desc: [
                        { required: true, message: 'Please enter a personal introduction', trigger: 'blur' },
                        { type: 'string', min: 20, message: 'Introduce no less than 20 words', trigger: 'blur' }
                    ]
                }
            }
        },
        methods: {
            handleSubmit (name) {
                this.$refs[name].validate((valid) => {
                    if (valid) {
                        this.$Message.success('Success!');
                    } else {
                        this.$Message.error('Fail!');
                    }
                })
            },
            handleReset (name) {
                this.$refs[name].resetFields();
            }
        }
    }
</script>

<style lang='less'>
	@import '~common/less/variable';
	.create-instance{
		background-color: @whiteBackground;
		padding: 10px 15px;
		//设置tab栏字体大小
		.ivu-tabs{
			.ivu-tabs-ink-bar{
				background-color: transparent;
			}
			.ivu-tabs-tab{
				font-size: 18px;
				line-height:27px;
				position: relative;
				padding-left: 36px;
				.ivu-icon{
					position: absolute;
					left: 16px;
					font-size: 22px;
					line-height: 27px;
					width: 22px;
					margin: 0;
				}
				&.ivu-tabs-tab-active{
					border-bottom: 3px solid @blueMainColor;
				}
			}
		}
		// 设置进度条
		.process{
			margin:6px 0;
		}
		.inst-panel{
			padding: 20px 380px 0 0;
			.choose-setting{
				.create-step{
					.input-style{
						min-width: 300px;
						max-width: 400px;
					}
					.com-width{
						min-width: 400px;
						max-width: 480px;
					}
					.create-info{
						top:8px;
						right: 0;
						width: 350px;
						border-left: 1px dotted @shrinkBtnColor;
						.info-title{
							margin: 0 8px;
							line-height: 30px;
							font-size: 16px;
							border-bottom: 1px solid @grayLineColor;
						}
						.info-panel{
							padding:15px 10px 15px 15px;
							font-size: 14px;
						}
						.btn-group{
							padding: 5px 10px;
							button{
								width: 90px;
							}
						}
					}
				}
			}
		}
	}
</style>


