<template>
	<div class="cloud-service">
		<second-slider v-show='sliderItem.hasMenu' :sliderItem="sliderItem" :isShow="isShowMenu" @hidden='hiddenMenu'></second-slider>
		<div :class="{extendLeft:isShowMenu,shrinkLeft:!isShowMenu}">
			<router-view></router-view>
		</div>
	</div>
</template>

<script>
	import secondSlider from 'base/second-slider/second-slider';
	export default {
		data(){
			return{
				isShowMenu:true,
				sliderItem:{}
			}
		},
		components:{
			secondSlider
		},
		created(){
			this.sliderItem = this.$route.params;
			this.isShowMenu = this.sliderItem.hasMenu;
		},
		methods:{
			hiddenMenu(isShow){
				this.isShowMenu = isShow;
			}
		}
	};
</script>

<style scoped lang='less'>
	@import '~common/less/variable';

	.cloud-service{
		width: 100%;
		border: 10px solid @colorBackground;
		border-left: 15px solid @colorBackground;
		background-color: #fff;
	}
	.shrinkLeft{
		height: 100%;
		padding-left: 0px;
	}
	.extendLeft{
		height: 100%;
		margin-left: 145px;
		border-left: 15px solid @colorBackground;
	}
</style>


