<template>
	<div class="exclusive-cloud">
		<second-slider v-show='sliderItem.hasMenu' :sliderItem="sliderItem" :isShow="isShowMenu" @hidden='hiddenMenu'></second-slider>
		<div :class="{extendLeft:isShowMenu,shrinkLeft:!isShowMenu}">
			<router-view></router-view>
		</div>
	</div>
</template>

<script>
	import secondSlider from 'base/second-slider/second-slider';
	export default {
		data(){
			return{
				isShowMenu:true,
				sliderItem:{}
			}
		},
		components:{
			secondSlider
		},
		created(){
			this.sliderItem = this.$route.params;
			this.isShowMenu = this.sliderItem.hasMenu;
		},
		methods:{
			hiddenMenu(isShow){
				this.isShowMenu = isShow;
			}
		}
	};
</script>

<style scoped lang='less'>
	@import '~common/less/variable';

	.exclusive-cloud{
		width: 100%;
		padding: 15px;
		background-color: @colorBackground;
	}
	.shrinkLeft{
		padding-left: 0px;
		// padding-bottom: 20px;
	}
	.extendLeft{
		padding-left: 160px;
		// padding-bottom: 20px;
	}
</style>


