import Vue from 'vue';
import Router from 'vue-router';
import CenterCont from 'base/content/center-content';
import HomeShow from 'components/home-show/home-show';
import cloudService from 'components/computingResource/cloud-service/cloud-service';
import instantList from 'components/computingResource/cloud-service/instance-list/instance-list';
import disk from 'components/computingResource/cloud-service/disk/disk';
import os from 'components/computingResource/cloud-service/os/os';
import exclusiveCloud from 'components/computingResource/exclusive-cloud/exclusive-cloud';
import excCloud from 'components/computingResource/exclusive-cloud/exc-cloud/exc-cloud';

Vue.use(Router);

export default new Router({
  mode: 'history',
  base: '/console',
  routes: [
  	{
  		path:'/index',
  		name:'index',
  		component:CenterCont,
  		children:[{
  			path:'/home',
  			name:'home',
  			component:HomeShow
  		},
  		{
	    	path:'/cloudService',
	    	name: 'cloudService',
	    	component: cloudService,
	    	children:[{
	    		path:'/InstanceList',
	    		name:'InstanceList',
	    		component:instantList
	    	},{
	    		path:'/disk',
	    		name:'disk',
	    		component:disk
	    	},{
	    		path:'/os',
	    		name:'os',
	    		component:os
	    	}]
    	},
    	{
    		path:'/exclusiveCloud',
    		name:'exclusiveCloud',
    		component:exclusiveCloud,
    		children:[{
    			path:'/excCloud',
	    		name:'excCloud',
	    		component:excCloud
    		}]
    	}]
  	}
  ]
});



