<template>
	<div class='left-slider p-f'>
		<div class="logo ta-c" :class="{'pull-logo':!isExtend}">
			<Icon type="ios-cloud-outline icon-logo va-m"></Icon>
			<span v-show='isExtend' class='text-logo va-m'>NO Cloud</span>
		</div>
		<div class="pull-extend p-a ta-c c-p" @click="$store.commit('invert')">
			<Icon type="navicon"></Icon>
			<!-- <Icon v-show='isExtend' type="chevron-left"></Icon> -->
			<!-- <Icon v-show='isExtend' type="arrow-return-left"></Icon> -->
			<!-- <Icon v-show='!isExtend' type="arrow-return-right"></Icon> -->
		</div>
		<Menu v-if='isExtend' theme="dark" class='left-menu' active-name="1" :open-names="[opendNum]" accordion>
			<MenuItem name="1" class='p-r left-menu-title first'>
				<router-link tag='li' class='single-item' :to="{name:'home'}">
					<Icon type="ios-home-outline" class='left-menu-icon va-m p-a'></Icon>
                	<span class="left-menu-text">控制台首页</span>
				</router-link>
            </MenuItem>
	        <Submenu name="2" class='left-menu-title'>
	            <template slot="title" class='p-r'>
	            	<Icon type="ios-calculator-outline" class='left-menu-icon va-m p-a'></Icon>
	                <span class="left-menu-text">云计算与网络</span>
	            </template>
	            <MenuItem name="2-1" class='left-menu-item'>
					<router-link tag='li' :to="{name:'InstanceList',params:{title:'云服务器',hasMenu:true,data:{'实例':'/InstanceList','硬盘':'/disk','快照':'/os'}}}">
						<span>云服务器</span>
					</router-link>
	            </MenuItem>
	            <MenuItem name="2-2" class='left-menu-item'>
					<router-link tag='li' :to="{name:'excCloud',params:{title:'专属云',hasMenu:true,data:{'专属宿主机':'/excCloud'}}}">
						<span>专属云</span>
					</router-link>
            	</MenuItem>
	        </Submenu>
	        <!-- <Submenu name="3" class='left-menu-title'>
	            <template slot="title" class='p-r'>
	                <Icon type="ios-speedometer-outline" class='left-menu-icon va-m p-a'></Icon>
	                <span class='left-menu-text'>云监控</span>
	            </template>
	            <MenuItem name="3-1" class='left-menu-item'>
	            	<router-link tag='li' :to="{name:'InstanceList',params:{title:'云服务器',hasMenu:true,data:{'实例':'/InstanceList','硬盘':'/disk','快照':'/os'}}}">
						<span>云服务类别</span>
					</router-link>
	            </MenuItem>
	            <MenuItem name="3-2" class='left-menu-item'>
	            	<router-link tag='li' :to="{name:'home'}">
						<span>告警策略</span>
					</router-link>
				</MenuItem>
                <MenuItem name="3-3" class='left-menu-item'>
                	<router-link tag='li' :to="{name:'home'}">
						<span>自定义面板</span>
					</router-link>
				</MenuItem>
                <MenuItem name="3-4" class='left-menu-item'>
                	<router-link tag='li' :to="{name:'home'}">
						<span>实例组管理</span>
					</router-link>
				</MenuItem>
                <MenuItem name="3-5" class='left-menu-item'>
                	<router-link tag='li' :to="{name:'home'}">
						<span>联系人管理</span>
					</router-link>
				</MenuItem>
	        </Submenu> -->
	    </Menu>
	    <!-- 收缩后的侧边栏 -->
	    <div v-if='!isExtend' class="small-menu">
	    	<ul>
	    		<li class='small-menu-title ta-c c-p p-r'>
	    			<Icon type="ios-home-outline" class='small-menu-icon'></Icon>
	    			<ul class='small-menu-info p-a'>
	    				<li class="info-title ta-l" @click="changeNumber('')">
	    					<router-link tag='div' :to="{name:'home'}">
			    				<span>控制台首页</span>
			    			</router-link>
			    		</li>
	    			</ul>
	    		</li>
	    		<li class='small-menu-title ta-c c-p p-r'>
	    			<Icon type="ios-calculator-outline" class='small-menu-icon'></Icon>
	    			<ul class='small-menu-info p-a'>
	    				<li class="info-title ta-l c-d">云计算与网络</li>
	    				<li class="info-item ta-c c-p" @click="changeNumber('2')">
	    					<router-link tag='div' :to="{name:'InstanceList',params:{title:'云服务器',hasMenu:true,data:{'实例':'/InstanceList','硬盘':'/disk','快照':'/os'}}}">
								<span>云服务器</span>
							</router-link>
						</li>
	    				<li class="info-item ta-c c-p" @click="changeNumber('2')">
	    					<router-link tag='div' :to="{name:'excCloud',params:{title:'专属云',hasMenu:true,data:{'专属宿主机':'/excCloud'}}}">
								<span>专属云</span>
							</router-link>
						</li>
	    			</ul>
	    		</li>
	    	</ul>
	    </div>
	</div>
</template>
<script>
	export default {
		data(){
			return{
				opendNum:''
			}
		},
		computed:{
			isExtend(){
				return !this.$store.state.isPull;
			}
		},
		methods:{
			changeNumber(num){
				this.opendNum = num;
			}
		}
	};
</script>

<style scoped lang='less'>
	@import "~common/less/variable.less";
	.left-slider{
		height:100%;
		top:0px;
		z-index: 100;
		.logo{
			width: 219px;
			height: 59px;
			line-height: 59px;
			background-color: @logoColor;
			border-bottom: 1px solid #a40011;
			border-right: 1px solid #a40011;
			color:#fff;
			.icon-logo{
				font-size: 50px;
				font-weight: 700;
			}
			.text-logo{
				padding-left: 5px;
				font-size: 18px;
			}
		}
		.pull-logo{
			width: 60px !important;
			height: 60px !important;
		}
		.pull-extend{
			top:0;
			right:-39px;
			width: 40px;
			line-height: 60px;
			background-color: @darkBackground;
			font-size: 30px;
			color:#f0f0f0;
			// .pull-icon{
			// 	font-size: 24px;
			// }
		}
		.left-menu{
			height: 100%;
			width: 220px !important;
			background: @blackBackground;
			.left-menu-title{
				line-height: 22px;
				color:rgba(255,255,255,.7);
				border:none;
				&.first{
					padding: 0;
					li{
						padding: 14px 24px;
					}
				}
				.single-item{
				    &.router-link-active{
				    	color: #2d8cf0;
						border-right: 2px solid #2d8cf0;
				    }
				}
				.left-menu-icon{
					font-size: 22px;
					left: 24px;
				}
				.left-menu-text{
					margin-left: 30px;
				}
				.left-menu-item{
					line-height: 18px;
					padding: 0;
					background-color: #495060 !important;
					li{
						padding: 14px 43px;
						&.router-link-active{
							color: #fff;
    						border-right: none;
    						background-color: #2d8cf0!important;
						}
					}
				}
			}
		}
		.small-menu{
			height: 100%;
			width: 60px !important;
			background: @blackBackground;
			.small-menu-title{
				height: 50px;
				line-height: 22px;
				padding: 14px 10px;
				.small-menu-icon{
					font-size: 22px;
					color:rgba(255,255,255,.7);
				}
				.small-menu-info{
					top:0;
					right:-140px;
					width: 140px;
					background: @blackBackground;
					display: none;
					.info-title{
						background-color: #444;
						height: 50px;
						line-height: 50px;
						font-size: 14px;
						padding-left: 10px;
						color: #fff;
						.router-link-active{
							color: #2d8cf0;
						}
					}
					.info-item{
						height: 36px;
						line-height: 36px;
						color:#e0e0e0;
						font-size: 14px;
						background-color: #555;
						&:hover{
							background-color: #666;
						}
						.router-link-active{
							color: #2d8cf0;
						}
					}
				}
				&:hover{
					background-color: #444;
					.small-menu-icon{
						color:#fff;
					}
					.small-menu-info{
						display: block;
					}
				}
				
			}
		}
	}
</style>



