<template>
	<div class="search p-r">
		<input type="text" class='search-input' placeholder='Search' v-model='value' @keyup.enter='Keysearch'>
		<div class="p-a d-b ta-c c-p search-icon" @click='Clicksearch'>
			<Icon type="ios-search"></Icon>
		</div>
	</div>
</template>

<script>
	export default {
		data(){
			return{
				value:''
			}
		},
		methods:{
			Keysearch(){
				this.$emit('search-item',this.value);
			},
			Clicksearch(){
				this.$emit('search-item',this.value);
			}
		}
	}
</script>

<style scoped lang='less'>
	@import '~common/less/variable';
	.search{
		border-radius: 5px;
		border:1px solid #ccc;
		.search-input{
			width: 230px;
			font-size: 14px;
			line-height: 22px;
			padding: 7px 32px 7px 8px;
			border-radius: 5px;
			outline: 0;
			&::-webkit-input-placeholder{
				color:@shrinkBtnColor;
			}
		}
		.search-icon{
			top:0;
			right: 0;
			width: 32px;
			font-size: 18px;
			line-height: 36px;
			// background-color: @whiteBackground;
			&:hover{
				background-color: #dedede;
			}
		}
	}
</style>


